@import url('https://fonts.googleapis.com/css2?family=Scheherazade+New&display=swap');

:root {
    --main-bg-color: #2874f0;
  }
  

*{margin: 0;padding: 0;}
header{
    background-color: var(--main-bg-color);
    font-family: 'Scheherazade New', serif;
    
}
nav{
    display: flex;
    align-items: center;
    background: cornflowerblue;
}
.logo{
    display: flex;
    align-items: center;
}
.logo img{
    width: 150px;
    height: 100px;
    padding: 0 30px;
}
.btn1{
    width: 20px;
    height:20px;
    padding: 3px;
    color: darkblue;
}
.logo img:hover{
    width: 155px;
    padding: 0 30px;
}
nav ul{
    height: 50px;
    margin-bottom: 0px;
    display: flex;
    justify-content: flex-start ;
    align-items: center;
}
nav ul li{
    list-style: none;
    padding: 0 15px;
}
nav ul li a{
    color: white;
    text-decoration: none; 
    align-items: center;
}
nav ul li a:hover{
    font-weight: bold;
    align-items: center;
}
nav .searchBar {
    display: flex;
    align-items: center;
}
nav .searchBar input{
    height: 30px;
    width: 30vw;
    border-radius: 2px;
    margin: 0 5px 0 30px;
    border: hidden;
    padding: 0 12px;
}
nav .searchBar button{
    font-family: 'Scheherazade New', serif;
}
.container{
    min-height: calc(100vh - 50px - 7vh);
}
.slider{
    margin: 10px auto;
    width: 90vw;
    display: flex;
    justify-content: center;
}
.slider img{
    display: flex;
    width: 80vw;
}
footer{
    background-color: var(--main-bg-color);
    height: 7vh;
    color: white;
    font-family: 'Scheherazade New', serif;
}