.container {
position: relative;
width: 420px;
border: #fff solid 2px;
border-radius: 4px;
height: 420px;
}


.book {
position: relative;
perspective: 630px;
perspective-origin: center 50px;
transform: scale(1.2);
filter: drop-shadow(0px 10px 5px rgba(0, 0, 0, 0.25));
}

.page {
width: 210px;
height: 300px;
background-color: #bbb;
position: absolute;
top: 0px;
right: 50%;
transform-origin: 100% 100%;
border: solid #555 2px;
background-size: 420px 300px;
background-position: center;
transform-style: preserve-3d;
}
.page:nth-child(1) {
transform: rotateX(60deg) rotateY(3deg);
}
.page:nth-child(2) {
transform: rotateX(60deg) rotateY(4.5deg);
}
.page:nth-child(3) {
transform: rotateX(60deg) rotateY(6deg);
animation: nextPage 10s infinite -9.6s steps(1);
background-size: 420px 300px;
background-position: -2px -2px;
}
.page:nth-child(4) {
transform: rotateX(60deg) rotateY(177deg);
}
.page:nth-child(5) {
transform: rotateX(60deg) rotateY(175.5deg);
}
.page:nth-child(6) {
transform: rotateX(60deg) rotateY(174deg);
overflow: hidden;
}
.page:nth-child(6)::after {
content: "";
width: 210px;
height: 300px;
position: absolute;
top: 0px;
right: 0%;
transform-origin: center;
transform: rotateY(180deg);
animation: nextPage 10s -8s infinite steps(1);
background-size: 420px 300px;
background-position: 100% -2px;
}
@keyframes nextPage {
0% {
background-image: url(/static/media/1.1b3a65ec.svg);
}
20% {
background-image: url(/static/media/1.1b3a65ec.svg);
}
40% {
background-image: url(/static/media/1.1b3a65ec.svg);
}
60% {
background-image: url(/static/media/1.1b3a65ec.svg);
}
80% {
background-image: url(/static/media/1.1b3a65ec.svg);
}
}

.gap {
width: 10px;
height: 300px;
background: none;
transform: rotateX(60deg);
transform-origin: bottom;
position: absolute;
top: 0px;
left: calc(50% - 5px);
}
.gap::after {
content: "";
position: absolute;
bottom: 0;
left: 50%;
transform: translate(-50%, 50%);
background-color: #555;
width: 10px;
height: 5px;
border-radius: 50%;
}

.pages, .flips {
transform-style: preserve-3d;
}

.flip {
width: 32px;
height: 300px;
position: absolute;
top: 0px;
transform-origin: 100% 100%;
right: 100%;
border: solid #555;
border-width: 2px 0px;
perspective: 4200px;
perspective-origin: center;
transform-style: preserve-3d;
background-size: 420px 300px;
}
.flip::after {
content: "";
position: absolute;
top: 0px;
right: 0%;
width: 100%;
height: 100%;
transform-origin: center;
background-size: 420px 300px;
}
.flip.flip1 {
right: 50%;
animation: flip1 2s infinite ease-in-out;
border-width: 2px 2px 2px 0;
}
.flip.flip1::after {
animation: nextFlip1 10s -8s infinite steps(1);
}
.flip:not(.flip1) {
right: calc(100% - 2px);
top: -2px;
transform-origin: right;
animation: flip2 2s ease-in-out infinite;
}
.flip.flip2::after {
animation: nextFlip2 10s -8s infinite steps(1);
}
.flip.flip3::after {
animation: nextFlip3 10s -8s infinite steps(1);
}
.flip.flip4::after {
animation: nextFlip4 10s -8s infinite steps(1);
}
.flip.flip5::after {
animation: nextFlip5 10s -8s infinite steps(1);
}
.flip.flip6::after {
animation: nextFlip6 10s -8s infinite steps(1);
}
.flip.flip7::after {
animation: nextFlip7 10s -8s infinite steps(1);
}
.flip.flip7 {
width: 30px;
border-width: 2px 0px 2px 2px;
}
.flip.flip7::after {
animation: nextFlip7 10s -8s infinite steps(1);
}
