@media screen and (max-width: 900px){
    body{
        background-color: rgb(243, 241, 241);
    }
    nav{
        flex-direction: column;
    }
    nav .searchBar input{
        width: 50vw;
    }
    nav .logo{
        justify-content: center;
        align-items: center;
        
        /* margin: 10px 0 0 0; */
    }
    nav ul{
        height: 30px;
        justify-content: center;
    }
    nav .searchBar{
        justify-content: center;
        margin: 5px;
    }
    .slider{
        display: flex;
        justify-content: flex-start
    }
    .slider img{
        
        width: 200px;
    }
}   